.result-page {
  width: 100%;
  min-height: 300px;
  margin-top: 20px;
  padding: 20px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  font-size: 30px;
  text-align: center;
  line-height: 35px;
  animation: show 0.6s linear;

  display: none;

  & > * {
    width: 100%;
    display: none;
  }
}

.result-page__btns-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 130px;
  
  button {
    height: 100px;
    width: 300px;

    border-radius: 10px;
    border: none;
    background: #ffffff;
    box-shadow: 0px 0px 10px 3px #00bc8c;
    
    font-family: baldur;
    font-size: 40px;
    color: #00bc8c;
  }

  @media screen and (max-width: 992px) {
    gap: 80px;
  }

  @media screen and (max-width: 768px) {
    gap: 50px;
  }

  @media screen and (max-width: 576px) {
    gap: 30px;
  }
}

.result-page__btn-new {
  &:not(:hover) {
    animation: puls 0.7s linear infinite;
  }
}

@keyframes puls {
  25% {
    transform: scale(0.95);
  }

  75% {
    transform: scale(1.05);
  }
}


